<template>
  <transition name="showBlockText">
    <div class="Text plr" v-if="text">
        <div class="Text-text">
          {{ text }}
        </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'Texts',
  data () {
    return {}
  },
  computed: {
    text: function () {
      return this.$store.state.text
    }
  }
}
</script>

<style lang="sass">
  .Text-text
    margin: .5em 0 0;
    text-align: center;
    color: #666;

  .showBlockText-enter-active, .showBlockText-leave-active
    transition: all .5s ease;
    position: relative;

  .showBlockText-enter
    margin-top: -1.7em;
    opacity: 0;

  .showBlockText-leave-to
    margin-top: -1.7em;
    opacity: 0;

</style>



