<template>
  <div>
    <div class="form Game-form plr">
      <transition name="showBlock">
        <div v-if="!gameLinkCopied" class="Game-link">
          <p>
            Send this link to&nbsp;the second captain <nobr>({{ secondName }})</nobr> to&nbsp;join your game.
          </p>
          <div class="form-row">
            <label for="gameLink">Link</label>
            <input type="text" readonly v-model="gameLink" id="gameLink">
          </div>
          <a class="button" v-clipboard="gameLink" @success="handleCopy">Copy link</a>
        </div>
        <div v-else class="Game-link-copied">
          <p>
            The link is copied.
          </p>
        </div>
      </transition>
      <div class="Game-new">
        <a class="button grey" @click="handleNew">New game</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Game',
  data () {
    return {
      gameLink: this.gameLinkF(this.$store.state.game._id),
      secondName: this.secondNameF(),
      gameLinkCopied: false
    }
  },
  computed: {
    gameId: function () {
      return this.$store.state.game._id
    }
  },
  methods: {
    gameLinkF (id) {
      return location.origin + '/g/' + id
    },
    secondNameF () {
      return this.$store.state.game.players[this.$store.state.players[1]].user.name
    },
    handleCopy (e) {
      this.gameLinkCopied = true
    },
    handleNew () {
      this.gameLinkCopied = false
      this.$store.dispatch('newGame')
    }
  },
  created () {
    this.$store.dispatch('saveGame')
  },
  watch: {
    gameId (id) {
      this.gameLink = this.gameLinkF(id)
      this.secondName = this.secondNameF()
    }
  }
}
</script>
<style lang="sass">
  .Game-form
    padding-top: 1em;

  .showBlock-enter-active, .showBloc-leave-active
    transition: all .5s ease;
    position: relative;

  .showBlock-enter
    margin-top: -1.7em;
    opacity: 0;

  .showBlock-leave-to
    margin-top: 1.7em;
    opacity: 0;

  .Game-new
    padding-top: 1em;

</style>



